import React from "react"
import { Link } from "gatsby"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

const IndexPage = ({
  data: {
    allMarkdownRemark: { edges },
  },
}) => (
  <Layout>
    <SEO title="Home" />
    <div>
    <h1>Welcome to GeroOf MyPage</h1>
    <ul>
      <li><Link to="/about/">about</Link></li>
      <li><Link to="/geroof.tumblr.com/">過去ブログアーカイブ</Link></li>
      <li><Link to="/gatsby/">gatsby入門記録</Link></li>
    </ul>
    </div>
    <div>
      <h3>Posts</h3>
      <ul>
      {edges
      .filter(edge => edge.node.frontmatter.slug.match("posts"))
      .map(edge => <li><Link to={edge.node.frontmatter.slug}>{edge.node.frontmatter.date} {edge.node.frontmatter.title}</Link></li>)}
      </ul>
    </div>
  </Layout>
)

export default IndexPage

export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          excerpt(pruneLength: 250)
          frontmatter {
            date(formatString: "YYYY/MM/DD")
            slug
            title
          }
        }
      }
    }
  }
`